@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap');

html{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    font-size: 10px;
}

*,*:before, *:after{
    box-sizing: inherit;
    font-family: inherit;
}

body {
    margin: 0;
    overflow-x: hidden;
}


