@mixin card {
    width: 270px;
    margin: 0 auto;
    -webkit-box-shadow: 0px 4px 13px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 4px 13px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 4px 13px 0px rgba(0,0,0,0.75);
    border-radius: 2.5px;
    overflow: hidden;
  }


